$('#checked-click').click(function(){
		console.log($('.auto-login-checkbox').is(':checked'));

	if ($('.auto-login-checkbox').is(':checked')) {
		console.log('done');
		$('.faked-checkbox').css({
			background: 'url(images/checked.jpg)'
			
		})
	}
	else{
		$('.faked-checkbox').css({
			background: 'url(images/unchecked.jpg)'
			
		})
	}

});



$('.sign-up-box').height(0).css("opacity",0);
$('#right-now-sign-up').click(
	function(){
		$('.sign-up-box').height(424).css("opacity",1);
		$('.sign-in-box').height(0).css("opacity",0)
	}
);

	